.device_log {
  margin-top: 20px;
}
.log_col {
  box-shadow: 1px 0px 3px #e7eaec;
  /* padding: 0 !important; */
  border-left: 1px solid #e7eaec;
  /* padding-left: 10px; */
  border-color: #e7eaec;
  border-top: 4px solid #e7eaec;
  overflow-y: auto;
}
.log_col .ibox-title {
  background-color: #ffffff;
  /* border-color: #e7eaec; */
  -webkit-border-image: none;
  border-image: none;
  border-style: solid solid none;
  border-width: 0;
  color: inherit;
  margin-bottom: 0;
  padding: 14px 0px 7px;
  min-height: 48px;
  border-bottom: 1px solid #e7eaec;
}
.log_col > div:nth-child(2) {
  margin: 60px 0 10px 0;
}
.log_col dl {
  margin: 10px;
  background: #f6f9fd;
  padding: 10px 15px;
}
.log_col dl dt {
  display: inline-block;
}
.log_col dl dt span {
  margin-left: 20px;
}
.log_col dl dd {
  display: block;
  padding-left: 30px;
  text-align: left;
}
.log_col .time-line-content {
  display: flex;
}
.log_col .time-line-content .right {
  border: 1px solid #333;
  border-radius: 7px;
  /* position: relative; */
  margin-top: -29px;
  margin-left: 16px;
  /* width: 44%; */
  float: left;
  border: 1px solid #d4d4d4;
  border-radius: 18px;
  padding: 20px;
  position: relative;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.175);
}
.log_col .time-line-content .right::before {
  position: absolute;
  top: 26px;
  right: -15px;
  display: inline-block;
  border-top: 15px solid transparent;
  border-left: 15px solid #ccc;
  border-right: 0 solid #ccc;
  border-bottom: 15px solid transparent;
  content: " ";
  border-left-width: 0;
  border-right-width: 15px;
  left: -15px;
  right: auto;
}
.log_col .time-line-content .right::after {
  position: absolute;
  top: 27px;
  right: -14px;
  display: inline-block;
  border-top: 14px solid transparent;
  border-left: 14px solid #fff;
  border-right: 0 solid #fff;
  border-bottom: 14px solid transparent;
  content: " ";
  border-left-width: 0;
  border-right-width: 14px;
  left: -14px;
  right: auto;
}
.statistics_log .ant-timeline-item {
  position: relative;
  margin: 0;
  padding: 0 0 20px;
  font-size: 14px;
  list-style: none;
  padding-top: 30px;
}
.statistics_log .ant-timeline-item .right {
  top: -30px;
}
.table-content-bk .breaken-table {
  text-align: center;
}
.table-content-bk .breaken-table table {
  box-sizing: border-box;
  border: 1px solid #ccc;
  padding: 0;
  box-shadow: 1px 1px 5px #ccc;
}
.table-content-bk .breaken-table table th {
  padding: 10px;
  background: #f8f8f8;
  box-sizing: border-box;
}
.table-content-bk .breaken-table table tr {
  padding: 0;
  box-sizing: border-box;
}
.table-content-bk .breaken-table table td {
  padding: 0;
  box-sizing: border-box;
}
.table-content-bk .breaken-table table .breaken-content {
  padding: 10px;
}
.table-content-bk .breaken-table table .breaken-result {
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;
}
.table-content-bk .breaken-table table .breaken-detail {
  padding: 0;
}
.table-content-bk .breaken-table table .breaken-detail tr td {
  border: 1px solid #ccc;
  flex: 1 1;
  padding: 10px;
  border-Left: none;
  border-bottom: none;
  vertical-align: middle;
}
.table-content-bk .breaken-table table .breaken-detail tr td:nth-child(2) {
  border-right: none;
}
.table-content-bk .breaken-table table .breaken-detail tr:nth-child(1) td {
  border-top: none;
}
